import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	props: {
		// 商品id
		goodsId: {
			type: String,
			default: ''
		},
		// 分享的图片
		shareImg: {
			type: String,
			default: ''
		},
		// 分享标题
		shareTitle: {
			type: String,
			default: ''
		},
		// 分享内容
		shareContent: {
			type: String,
			default: ''
		},
		// 分享链接
		shareHref: {
			type: String,
			default: ''
		},
		//分享类型
		shareType: {
			type: Number,
			default: 1
		},
		//用户id
		userId: {
			type: String,
			default: ''
		},
		//拼团id
		groupId: {
			type: String,
			default: ''
		},
		//拼团的团队id
		teamId: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			providerList: [] // 分享通道 包含生成海报
		}
	},
	mounted() {
		
	},
	computed: {
		...mapState(['hasLogin', 'userInfo', 'applicationConfig'])
	},
	methods: {
		// 关闭弹出层
		close() {
			this.$emit('close')
		},
		// 生成海报
		createPoster() {
			let data = {
				shareType: this.shareType,
				productUuid: this.goodsId,
				groupBuyUuid: this.groupId,
				userUuid: this.userId,
				shareHref: this.shareHref
			}
			this.$api.request.goodsPoster(data, res => {
				if (res.body.status.statusCode === '0') {
					this.close();
					uni.navigateTo({
						url: '/pages/product/poster?poster=' + escape(res.body.data.posterUrl)
					})
				} else {
					this.$api.msg(res.body.status.errorDesc);
				}
			});
		}
	}
}
